body{margin:0;padding:0;font-family:'Open Sans', sans-serif;font-size:14px}p{font-weight:400}

.navbar-container{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between;padding:0 5%;border-bottom:1px solid #ebe1e1;background-color:#f3f6fd}.navbar-container h2{font-size:1em}.navbar-container .logo{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center}.navbar-container img{padding:5px;width:30px;height:30px}.navbar-container .menu{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}.navbar-container .menu .search-label{font-weight:700;margin-right:10px}.navbar-container .menu a{font-size:.8em;font-weight:700;margin:0 10px;text-decoration:none;color:#424242;margin:10px 10px}.navbar-container .menu a:hover{color:#ffa600}.navbar-container .menu input::-webkit-input-placeholder{color:rgba(255,255,255,0.37);font-weight:300;font-size:.8em}.navbar-container .menu input:-ms-input-placeholder{color:rgba(255,255,255,0.37);font-weight:300;font-size:.8em}.navbar-container .menu input::placeholder{color:rgba(255,255,255,0.37);font-weight:300;font-size:.8em}.navbar-container .menu input{margin-left:5px;border-radius:0.3em;border:0.1em solid rgba(255,255,255,0);background-color:#191E29;padding:2px 10px 5px 10px;box-shadow:1px 1px 8px 0px rgba(0,0,0,0.1)}.navbar-container .menu input:focus{border-radius:0.3em;border:0.1em solid #fff;background-color:#424242;padding:2px 10px 5px 10px;box-shadow:inset 1px 1px 8px 0px rgba(0,0,0,0.1)}.navbar-container .menu input:focus::-webkit-input-placeholder{font-weight:100;color:rgba(255,255,255,0.37)}.navbar-container .menu input:focus:-ms-input-placeholder{font-weight:100;color:rgba(255,255,255,0.37)}.navbar-container .menu input:focus::placeholder{font-weight:100;color:rgba(255,255,255,0.37)}.navbar-container .menu input:focus-visible{outline:none;color:white}.navbar-container .menu .navbar-btn{font-family:'Open Sans', sans-serif;font-weight:500;background-color:#ffa600;color:#fff;border:none;border-radius:3px;padding:5px 25px;transition:background-color .5s, color 0.5s}.navbar-container .menu .navbar-btn:hover{background-color:#fff;color:#ffa600}

