x;width:30px;margin-right:10px}.coin-symbol{text-transform:uppercase}.coin-data{display:-webkit-flex;display:flex;text-align:right;-webkit-justify-content:space-between;justify-content:space-between;width:100%}.coin-price{width:110px}.coin-volume{width:155px}.coin-marketcap{width:230px}.coin-percent{width:100px}.red{color:#f00606}.green{color:#11d811}

